import React from 'react';
import { RequestSteps } from "./RequestSteps";
import { Button } from "antd";
import RegInfo from './requestComponents/regInfo/RegInfo';
import s from './RequestSteps.module.scss';
import { Select } from 'antd';
import UserAbout from './requestComponents/userAbout/UserAbout';
import InfoSteps from './requestComponents/infoSteps/InfoSteps';
const { Option } = Select;





const Questionnaire = ({ nextStep }) => {
    const [valuePet, setValuePet] = React.useState('');
    const [valueChildren, setValueChildren] = React.useState('');

    const data = {
        numberStep: "1",
        title: " ",
        par1: "1.        : ,      ,    ;        .        ;",
        par2: "2. ,     ,   ;        . 1.       ;",
    }

    return (
        <section className={s.step1}>
            <div className="container">
                <div className={s.inner}>
                    <div className={s.quizeBlock}>
                        <RegInfo />
                        <div className={s.title}>  &nbsp;  </div>
                        <div className={s.aboutQuize}>
                            <div className={s.aboutquizeTitle}> </div>
                            <div className={s.aboutquizeSubtitle}>
                                       
                                       !
                            </div>
                        </div>
                        <div className={s.itemsQuize}>
                            <div className={s.item}>
                                <div className={s.selectTitle}>1.       ?</div>
                                <div className={s.selectItem}>
                                    <div className={s.selectSubtitle}>-</div>
                                    <Select className={s.select}>
                                        <Option value="" hidden></Option>
                                        <Option value="1">1</Option>
                                        <Option value="2">2</Option>
                                        <Option value="3">3</Option>
                                        <Option value="4">4</Option>
                                        <Option value="5+">5+</Option>
                                    </Select>
                                </div>
                            </div>
                            <div className={s.item}>
                                <div className={s.selectTitle}>2.      ?</div>
                                <div className={s.selectesWrap}>
                                    <div className={s.selectItem}>
                                        <div className={s.selectSubtitle}></div>
                                        <Select className={s.select}
                                            onChange={setValuePet}> value={valuePet}
                                            <Option value="" hidden></Option>
                                            <Option value=""></Option>
                                            <Option value=""></Option>
                                            <Option value=""></Option>
                                        </Select>
                                    </div>
                                    <div className={s.selectItem}>
                                        {valuePet === '' &&
                                            <div>
                                                <div className={s.selectSubtitle}></div>
                                                <input className={s.input} type="text" />
                                            </div>}
                                    </div>
                                </div>
                            </div>
                            <div className={s.item}>
                                <div className={s.selectTitle}>3.    ?</div>
                                <div className={s.selectItem}>
                                    <div className={s.selectSubtitle}></div>
                                    <Select className={s.select}>
                                        <Option value="" hidden></Option>
                                        <Option value="18-25">18-25</Option>
                                        <Option value="25-40">25-40</Option>
                                        <Option value="40-60">40-60</Option>
                                        <Option value="60+">60+</Option>
                                    </Select>
                                </div>
                            </div>
                            <div className={s.item}>
                                <div className={s.selectTitle}>4.     ,     ?</div>
                                <div className={s.selectesWrap}>
                                    <div className={s.selectItem}>
                                        <div className={s.selectSubtitle}></div>
                                        <Select value={valueChildren} className={s.select}
                                            onChange={setValueChildren}>
                                            <Option value="" hidden></Option>
                                            <Option value=""></Option>
                                            <Option value=",  ">,  </Option>
                                            <Option value=""></Option>
                                        </Select>
                                    </div>
                                    <div className={s.selectItem}>
                                        {valueChildren !== '' && valueChildren !== '' &&
                                            <div>
                                                <div className={s.selectSubtitle}>-</div>
                                                <Select className={s.select} name="" id="">
                                                    <Option value="" hidden></Option>
                                                    <Option value="1">1</Option>
                                                    <Option value="2">2</Option>
                                                    <Option value="3">3</Option>
                                                    <Option value="3+">3+</Option>
                                                </Select>
                                            </div>
                                        }
                                    </div>
                                </div>
                            </div>
                            <div className={s.item}>
                                <div className={s.selectTitle}>5.               ?</div>
                                <div className={s.selectesWrap}>
                                    <div className={s.selectItem}>
                                        <div className={s.selectSubtitle}></div>

                                        <Select className={s.select}>
                                            <Option value="" hidden></Option>
                                            <Option value="  ">  </Option>
                                            <Option value="  ">  </Option>
                                            <Option value="   ">   </Option>
                                        </Select>
                                    </div>
                                    <div className={s.selectItem}>
                                        <div className={s.selectSubtitle}>-</div>

                                        <Select className={s.select}>
                                            <Option value="" hidden></Option>
                                            <Option value="1">1</Option>
                                            <Option value="2">2</Option>
                                            <Option value="3">3</Option>
                                            <Option value="4">5</Option>
                                            <Option value="5+">5+</Option>
                                        </Select>
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div className={s.next}>
                            <div className={s.nextItem}></div>
                            <div className={s.nextItem}> </div>
                        </div>
                        <Button className={s.btnColor} type="primary" onClick={() => nextStep(RequestSteps.CHARACTERISTICS)}></Button>
                    </div>

                    <div className={s.infoBlock}>
                        <UserAbout name=" " eMail="aleksreshetnikov@gmail.com" />
                        <InfoSteps numberStep={data.numberStep} title={data.title} par1={data.par1} par2={data.par2} />
                    </div>
                </div>
            </div>
        </section >
    );
};

export default Questionnaire;